@mixin list-style-none {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

@mixin retina {
  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    @content;
  }
}

// Desktop first

@mixin vp-1919 {
  @media (max-width: $vp-1920 - 1) {
    @content;
  }
}

@mixin vp-1439 {
  @media (max-width: $vp-1440 - 1) {
    @content;
  }
}

@mixin vp-1279 {
  @media (max-width: $vp-1280 - 1) {
    @content;
  }
}

@mixin vp-1023 {
  @media (max-width: $vp-1024 - 1) {
    @content;
  }
}

@mixin vp-767 {
  @media (max-width: $vp-768 - 1) {
    @content;
  }
}

@mixin vp-374 {
  @media (max-width: $vp-375 - 1) {
    @content;
  }
}

@mixin mobile-only {
  @media (max-width: $vp-768 - 1) {
    @content;
  }
}

@mixin tablet-only {
  @media (max-width: $vp-1366 - 1) {
    @content;
  }
}

@mixin from-tablet-to-max {
  @media (min-width: $vp-768) {
    @content;
  }
}

@mixin from-tablet-to-desktop {
  @media (min-width: $vp-768) and (max-width: $vp-1366 - 1) {
    @content;
  }
}

@mixin desktop-only {
  @media (min-width: $vp-1366) {
    @content;
  }
}


